import React, {Component} from 'react';
import {Row, Col} from 'react-bootstrap';
import MissionHeader from "./MissionHeader";

class MissionContent extends Component {
    render() {
        return (
            <Row className="about-us">
                <MissionHeader/>
                <Col>
                    <p>Marka Dr. Fisher czy tradycj dobrego smaku i najwyszej jakoci produktu z wieloletnim
                        dowiadczeniem przygotowywania konserw rybnych. Nowoczesne metody produkcji zachowujce
                        wszystkie walory smakowe i odywcze przetwarzajcego surowca oraz dodatkiprzygotowane w
                        tradycyjny sposb daj niepowtarzalny smak caej gamie naszych produktw.
                    </p>
                    <p>Receptury konserw Dr. Fisher'a to wynik wieloletniej pracy, ktrej celem jest osigniecie
                        tak bardzo dzisiaj poszukiwanych i cenionych walorw smakowych z jednoczesnym zwrceniem uwagi
                        na
                        niskokaloryczno i wysok zawarto elementw niezbdnych do zdrowia i odpowiedniej diety
                        Zdrowie - nie musi uznacza nie smaczne - to motto receptur Dr. Fisher'a.
                        Caa linia produktw pod wzgldem smakowym przygotowana zostaa dla wielu odbiorcw, czy j
                        jedna cecha wsplna - dbaoc o skadniki czyli ryb najwyszej jakoci i dodatki.
                        Starannie przygotowane i tradycyjnie z zachowaniem wszystkich wartoci bioenergetycznych
                        i smakowych. Efektem dugich procesw przygotowania s produkty, ktre stay si niezbdnym
                        elementem
                        zdrowego odywiania. Jestemy jedyn firm, ktra przygotowuje swoje sosy z naturalnych
                        skadnikw
                        wedug orginalnych wieloletnich receptur. Marka Dr. Fisherjest gwarantem umiejtnego czenia
                        tradycji i dowiadczenia z dzisiejszymi trendami zdrowego odywania, propagowanego przez
                        mistrzw
                        kulinarnych, lekarzy, dietetykw i sportowcw.
                    </p>
                    <p>"Autentycznie z nad morza" to nie tylko haso, to adres. Nasze kutry dobijaj do nadbrzea
                        by wyadowa swj adunek prosto na nasze linie produkcyjne, gdzie wyspecjalizowany personel,
                        ktry od wielu pokole zajmuje si prztwrstwem ryb przygotowuje je z myl o Pastwa zdrowiu
                        i satysfakcji.
                    </p>
                    <p>Produkty Dr. Fisher to wiadomy wybr zdrowego odywania bez obaw, e walory smakowe
                        musz zej na drugi plan.
                    </p>
                </Col>
            </Row>
        );
    }
}

export default MissionContent;



